.navcontainer {
    background-color: #d0de1a;
}


.navbar {
    height: 10vh;
    position: fixed;
    display: flex;
    justify-content: space-between;
    align-items: center;
    top: 0;
    left: 0;
    background-color: #d0de1a;
    width: 98%;
}

.navbar>h4 {
    display: none;
}


.logostyle {
    width: 25%;
}

.mobileMenu {
    width: 90%;
    opacity: 0.7;
    z-index: 1;
    position: absolute;
    top: 0;
    left: 0;
    display: flex;
    flex-direction: column;
    justify-content: flex-start;
    align-items: start;
    height: 100vh;
    background-color: blue;
    transition: left 0.3s ease;
}

.mobileMenu>a {
    color: white;
    height: 30px;
    padding: 5px;
    text-decoration: none;
}

mobileMenu>a:hover {
    background-color: yellow;
}

.navbar>.menu-item {
    display: none;
}

.menu-item {
    display: flex;
    background-color: green;
    justify-content: space-evenly;
    align-items: center;
    width: 90%;
}

.menu-item>a {
    text-decoration: none;
    color: blue;
}

.menu-item>a:hover {
    color: yellow;
}


@media screen and (max-width: 450px) {
    .navbar {
        width: 94%;
        padding-right: 5px;
    }

    .mobileMenu {
        width: 80%;
    }

    .navbar>h4 {
        display: contents;
    }

    .logostyle {
        width: 40%
    }

}

@media screen and (min-width: 451px) {
    .navbar>.menu-item {
        display: contents
    }
}